<template>   
    <v-container>     
        <v-card>       
            <v-container grid-list-md mb-12>
            <h2 class="text-md-center">Order Transaction</h2>         
            <v-layout row wrap style="margin:10px">           
                <v-flex xs6 class="text-right">               
                    <v-text-field                 
                    v-model="keyword"                 
                    append-icon="mdi-search"                 
                    label="Search"                 
                    hide-details               
                    ></v-text-field>           
                </v-flex> 
                <v-flex>          
                </v-flex>            
                <v-flex offset-xs4>           
                    <v-btn               
                    depressed               
                    dark               
                    rounded         
                    style="text-transform: none !important;"               
                    color = "green accent-3"               
                    @click="dialog = true"               
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Add Cart               
                    </v-btn>           
                </v-flex>           
            </v-layout>
            <h3 class="text-md-center">Cart</h3>    
            
            <v-data-table             
            :headers="headers"             
            :items="users"             
            :search="keyword"             
            :loading="load"           
            >             
            <template v-slot:body="{ items }">               
                <tbody>                 
                    <tr v-for="(item) in items" :key="item.id">                   
                        <td>{{ item.item }}</td>                   
                        <td>{{ item.type }}</td>                   
                        <td>{{ item.quantity }}</td>                   
                        <td>{{ item.price }}</td>                 
                        <td >                     
                            <v-btn                        
                            icon                       
                            color="indigo"                       
                            light                       
                            @click="editHandler(item)"                     
                            >                       
                            <v-icon>mdi-pencil</v-icon>                     
                            </v-btn>                     
                            <v-btn 
                            icon                       
                            color="error"                       
                            light                       
                            @click="deleteData(item)"                     
                            >                       
                                <v-icon>mdi-delete</v-icon>                     
                            </v-btn>                   
                        </td>                 
                    </tr>               
                </tbody>             
            </template>           
        </v-data-table>
        <v-flex xs6 style="margin-left:100px" class="text-right"> 
        <v-btn               
                    depressed               
                    dark               
                    rounded         
                    style="text-transform: none !important;"               
                    color = "green accent-3"               
                    @click="dialog2 = true"               
                    >               
                    <v-icon size="18" class="mr-2">mdi-pencil-plus</v-icon>                   
                        Order Transaction               
                    </v-btn>
        </v-flex>
    </v-container>     
    </v-card>     
    <v-dialog v-model="dialog" persistent max-width="600px">       
        <v-card>         
            <v-card-title>           
                <span class="headline">Admin</span>         
                </v-card-title>         
                <v-card-text>           
                    <v-container>             
                        <v-row>                          
                                    <v-col cols="12" >                 
                                        <v-select
                                            :disabled="itemUp"
                                            v-model="form.item"
                                            :items="items"
                                            label="Item*"
                                            dense
                                        ></v-select>               
                                    </v-col>
                                    <v-col cols="12">                 
                                        <v-text-field label="Quantity*" v-model="form.quantity" required></v-text-field>               
                                    </v-col>           
                                    </v-row>           
                                </v-container>           
                                <small>*indicates required field</small>         
                            </v-card-text>         
                            <v-card-actions>           
                                <v-spacer></v-spacer>           
                                <v-btn color="blue darken-1" text @click="resetForm()">Close</v-btn>           
                                <v-btn color="blue darken-1" text @click="setForm()">Save</v-btn>         
                                </v-card-actions>       
                            </v-card>     
                        </v-dialog>     
    <v-dialog v-model="dialog2" persistent max-width="600px">       
        <v-card>         
            <v-card-title>           
                <span class="headline">Admin</span>         
                </v-card-title>         
                <v-card-text>           
                    <v-container>             
                        <v-row>                          
                                    <v-col cols="12">                 
                                        <v-text-field label="Name*" v-model="form.name" required></v-text-field>               
                                    </v-col>           
                                    <v-col cols="12">                 
                                        <v-select
                                            v-model="form.type"
                                            :items="items2"
                                            label="Type Transaction*"
                                            dense
                                        ></v-select>               
                                    </v-col>
                                    </v-row>           
                                </v-container>           
                                <small>*indicates required field</small>         
                            </v-card-text>         
                            <v-card-actions>           
                                <v-spacer></v-spacer>           
                                <v-btn color="blue darken-1" text @click="resetForm()">Close</v-btn>           
                                <v-btn color="blue darken-1" text @click="sendData2()">Order</v-btn>         
                                </v-card-actions>       
                            </v-card>     
                        </v-dialog>     
                    <v-snackbar       
                    v-model="snackbar" 
                    :color="color"       
                    :multi-line="true"       
                    :timeout="3000"     
                    >       
                    {{ text }}       
                    <v-btn         
                     dark         
                     text         
                     @click="snackbar = false"       
                    >         
                        Close       
                    </v-btn>     
                </v-snackbar>
                 
            </v-container> 
        </template> 
<script> 
import { log } from 'util'
export default {    
    data () {       
        return {  
            itemUp: false,
            menu2: false,      
            dialog: false,      
            dialog2: false,      
            keyword: '', 
            items: ['Bakso', 'Mie Ayam', 'Es Teh', 'Nasi Goreng', 'Mie Goreng', 'Sate', 'Es Jeruk', 'Aneka Jus' ],
            items2: ['Take Away', 'Dine In'],
            headers: [         
                {               
                    text: 'Item',               
                    value: 'item'             
                },             
                {               
                    text: 'Type',               
                    value: 'type'             
                },             
                {               
                    text: 'Quantity',               
                    value: 'quantity'             
                },             
                {               
                    text: 'Price',               
                    value: 'price'             
                },             
                {               
                    text: 'Aksi',               
                    value: null             
                }         
            ],         
            users: [],         
            snackbar: false,          
            color: null,         
            text: '',          
            load: false, 
            form: {            
                item : '',  
                quantity: '',        
                type: '',        
                name: ''        
            },         
            user : new FormData,         
            typeInput: 'new',        
            errors : '',         
            updatedId : '',       
        }     
    },     
    methods:{         
        getData(){
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }             
            var uri = this.$apiUrl + '/Cart/where/' + localStorage.getItem('id')
            this.$http.get(uri).then(response =>{                 
                this.users=response.data.message             
            })               
        },         
        sendData(){           
            this.user.append('item', this.form.item);             
            this.user.append('quantity', this.form.quantity);            
            var uri =this.$apiUrl + '/Cart/' + localStorage.getItem('id')
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();           
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            })         
        },         
        sendData2(){           
            this.user.append('name', this.form.name);             
            this.user.append('type', this.form.type);            
            var uri =this.$apiUrl + '/Transaction/' + localStorage.getItem('id')
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{               
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false;
                this.resetForm();
                this.$router.push({ name : "TransactionListController"})
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;           
            })         
        },         
        updateData(){   
            this.user.append('item', this.form.item);             
            this.user.append('quantity', this.form.quantity);      
            var uri = this.$apiUrl + '/Cart/' + localStorage.getItem('id') + '/' + this.form.item;             
            this.load = true             
            this.$http.post(uri,this.user).then(response =>{ 
                this.snackbar = true; //mengaktifkan snackbar               
                this.color = 'green'; //memberi warna snackbar               
                this.text = response.data.message; //memasukkan pesan ke snackbar               
                this.load = false;               
                this.dialog = false               
                this.getData(); //mengambil data user               
                this.resetForm();               
                this.typeInput = 'new';
            this.itemUp = true;         
            }).catch(error =>{               
                this.errors = error               
                this.snackbar = true;               
                this.text = 'Try Again';               
                this.color = 'red';               
                this.load = false;               
                this.typeInput = 'new';
            this.itemUp = true;            
            })         
        },         
        editHandler(item){           
            this.typeInput = 'edit';
            this.itemUp = true;       
            this.dialog = true;           
            this.form.item = item.item;           
            this.form.quantity = item.quantity;           
            this.updatedId = item.id;         
        },
        
        deleteData(item){ //mengahapus data
            // var config = {
            //     headers: {
            //         Authorization: 'Bearer ' + localStorage.getItem('token')
            //     }
            // }              
            var uri = this.$apiUrl + '/Cart/' + localStorage.getItem('id') + '/' + item.item; //data dihapus berdasarkan id 
            this.$http.delete(uri).then(response =>{                 
                this.snackbar = true;                 
                this.text = response.data.message;                 
                this.color = 'green'                 
                this.deleteDialog = false;                 
                this.getData();             
            }).catch(error =>{                 
                this.errors = error                 
                this.snackbar = true;                 
                this.text = 'Try Again';                 
                this.color = 'red'; 
            })         
        },
        
        setForm(){             
            if (this.typeInput === 'new') {               
                this.sendData()             
            } else {               
                this.updateData()             
            }         
        },         
        resetForm(){             
            this.form = {               
                name : '',               
                type : '',               
                item : '',
                quantity: ''             
            }
            this.dialog = false      
            this.dialog2 = false
            this.itemUp = false;       
        }     
    },     
    mounted(){         
        this.getData();     
        }, 
    } 
</script> 
  